<template>
  <img class="bg-slider" :src="theme.background">

  <div class="section-info">
    <div class="item item-01">
      <img :src="base_url + 'public/game/images/name.png'">
      <div class="item-info">{{ task.name }}</div>
      <div class="avatar"><img :src="getAvatarUser(account)"></div>
    </div>
    <div class="box">
      <div class="item item-02">
        <div class="effect-get-gift get-points" :data-status=" parseInt(numCoins) < 0 ? 'minus' : 'plus' "
             v-if="isGetCoins">
          <span class="plus">+</span>{{ numCoins }}
        </div>
        <img :src="base_url + 'public/game/images/vang.png'">
        <div class="item-info" :key="coins">{{ coins }}</div>
      </div>
      <div class="item item-03">
        <div class="effect-get-gift get-cards" v-if="isGetCards">
          +<img :src="base_url + 'public/game/images/card_empty.png'"/>
        </div>
        <img :src="base_url + 'public/game/images/the-bao.png'">
      </div>
      <div class="item item-03 btn-menu settings">
        <img :src="base_url + 'public/img/setting-btn.png'">
      </div>
      <div @click="closeGame()" style="cursor: pointer" class="item item-04">
        <img :src="base_url + 'public/game/images/x.png'">
      </div>
    </div>
  </div>

  <div class="slider" :id="theme.layout" v-if="isStart">

    <button v-if="showPrevGame && key>0" @click="prevGame" id="prev-game" class="prev-game slick-prev slick-arrow"
            aria-label="Previous" type="button">
      Previous
    </button>

    <div class="page" v-if="!loading">
      <Slide v-bind:checkAns="checkAnswer" :key="key" v-if="typeGame==0"></Slide>

      <Question v-bind:checkAns="checkAnswer" :key="key"
                v-if="typeGame==1"></Question>

      <Fill v-bind:checkAns="checkAnswer" :key="key" v-if="typeGame==2"></Fill>

      <Crossword v-bind:checkAns="checkAnswer" :key="key" v-if="typeGame==3"></Crossword>

      <Images v-bind:checkAns="checkAnswer" v-bind:fnNextGame="nextGame" v-bind:fnChangeGame="changeGame" :key="key"
              v-if="typeGame==4"></Images>

      <Card  v-bind:fnChangeGame="changeGame" :key="key" v-if="typeGame==5"></Card>

      <Chat v-bind:checkAns="checkAnswer" v-bind:fnChangeGame="changeGame" :key="key" v-if="typeGame==6"></Chat>

    </div>

    <button v-if="showNextGame" @click="nextGame" id="next-game" class="next-game slick-next slick-arrow"
            aria-label="Next"
            type="button">
      Next
    </button>

  </div> <!-- / slider -->
  <!--    <div class="slider" v-else>-->
  <!--        <button @click="startGame" class="btn-start">BT U</button>-->
  <!--    </div>-->
</template>

<script>
import {mapState} from 'vuex'

import Slide from "./type/Slide";
import Question from "./type/Question";
import Fill from "./type/Fill";
import Crossword from "./type/Crossword";
import Chat from "./type/Chat";
import Card from "./type/Card";
import Images from "./type/Image";

export default {
  name: 'Play Game',
  data() {
    return {
      checkAnswer: 0,
      base_url,
      showNextGame: 1,
      showPrevGame: 1,
      typeGame: -1,
      theme: {
        background: '',
        layout: ''
      },
      audioBackground: {},
    };
  },
  components: {Slide, Question, Fill, Crossword, Chat, Card, Images},
  methods: {
    // startGame() {
    //     this.audioBackground = playAudio(this.base_url + 'public/game/audio/bg_game.mp3', true);
    //     this.$store.commit("startGame");
    // },
    closeGame() {
      window.location.href = this.base_url;
    },
    nextGame() {
      $('#next-game').attr('disabled', 'disabled')
      $('#prev-game').attr('disabled', 'disabled')

      if (this.key == this.task.task_detail.length - 1) {
        this.showNextGame = false;
        this.showPrevGame = false;
      }

      if (this.game.is_done) {
        this.changeGame();
      } else if (this.game.type == 3) {
        this.checkAnswer = 1;
        this.audioAnswer();

        setTimeout(() => {
          this.checkAnswer = 3;
        }, 1000);

        setTimeout(() => {
          this.changeGame();
          this.checkAnswer = 0;
        }, 3000)

      } else {
        this.checkAnswer = 1;
        this.audioAnswer();
        setTimeout(() => {
          this.changeGame();
          this.checkAnswer = 0;
        }, 1000)
      }
    },
    prevGame() {
      this.$store.commit("changeGame", 'prev');
    },
    changeGame() {
      if (this.key == this.task.task_detail.length - 1) {
        setTimeout(() => {
          this.$store.dispatch("doneTasks");
        }, 2000)
      } else {
        this.$store.commit("changeGame", 'next');
      }
    },
    audioAnswer() {
      setTimeout(() => {
        let checkGame = this.$store.getters.correctAnswer;
        if (checkGame.isCorrect) {
          if (checkGame.isCorrect == 'right')
            new Audio(base_url + 'public/game/audio/' + checkGame.isCorrect + '.mp3').play();

          this.$store.commit("logPoint", checkGame.point);
        }
      }, 200)
    },
    getAvatarUser(account) {
      if (account.avatar == '') {
        return `public/game/images/avatar.png`;
      }

      return account.avatar;
    },
  },
  created() {
    this.$store.dispatch("trainingTask");
  },
  mounted() {
    let isAutoPlay = getCookie('dq_play_audio_background');
    // if (!isAutoPlay) {
    //   this.audioBackground = playAudio(this.base_url + 'public/game/audio/bg_game.mp3', true);
    // } else {
    //   this.audioBackground = {
    //     play: () => false,
    //     pause: () => false,
    //   }
    // }
    // let _this = this;
    // window.onfocus = function() {
    //     _this.audioBackground.play();
    // };
    //
    // window.onblur = function() {
    //     if (_this.isStart) _this.audioBackground.pause();
    // };
  },
  computed: mapState(['isStart', 'task', 'loading', 'game', 'key', 'coins', 'isGetCoins', 'numCoins', 'isGetCards', 'account']),
  watch: {
    task: {
      handler(task) {
        let key = parseInt(task.key);

        if (key <= 10) {
          this.theme.background = this.base_url + 'public/game/images/bgk1.png';
          this.theme.layout = 'round-1';
        } else if (key <= 20) {
          this.theme.background = this.base_url + 'public/game/images/bgk2.png';
          this.theme.layout = 'round-2';
        } else if (key <= 30) {
          this.theme.background = this.base_url + 'public/game/images/bgk3.png';
          this.theme.layout = 'round-3';
        } else if (key <= 40) {
          this.theme.background = this.base_url + 'public/game/images/bgk4.png';
          this.theme.layout = 'round-4';
        } else if (key <= 50) {
          this.theme.background = this.base_url + 'public/game/images/bgk5.png';
          this.theme.layout = 'round-5';
        } else if (key <= 60) {
          this.theme.background = this.base_url + 'public/game/images/bgk6.png';
          this.theme.layout = 'round-6';
        } else if (key <= 70) {
          this.theme.background = this.base_url + 'public/game/images/bgk7.png';
          this.theme.layout = 'round-7';
        } else if (key <= 80) {
          this.theme.background = this.base_url + 'public/game/images/bgk8.png';
          this.theme.layout = 'round-8';
        }
      }
    },
    game: {
      handler(game) {

        if (typeof game === "undefined") {
          window.location.href = base_url + 'hocbai?status=error';
          return;
        }

        let type = this.typeGame = parseInt(game.type);

        if (type === 6) {
          this.showPrevGame = 0;
          this.showNextGame = 0;
        } else {
          this.showPrevGame = 1;
          this.showNextGame = 1;
        }

        setTimeout(() => {
          $('#next-game').removeAttr('disabled')
          $('#prev-game').removeAttr('disabled')
        }, 300)
      }
    }
  }
};
</script>
